import { Col, Row, Typography, Image } from "antd";
import React from "react";

const Projects: React.FC = () => (
  <Row 
  justify='center' 
  align='middle' 
  style={{
    alignItems: 'center',
    textAlign:'center',
    flexDirection:'column',
  }}>
    <div id='projects-site'></div>
    <Typography.Title level={1}>My Projects</Typography.Title>
    <Row
    style={{
      flexDirection: 'row',
      justifyContent: 'center',
      width: '100%',
    }}>
      <Col span={6}>
        <Image
          width={200}
          src={require('../../assets/images/projectpreview/Capture.PNG')}
          fallback="data:image/png;base64,"
        />
        <a href='https://abdul-hakam.web.app/projects/themealdb/'><Typography.Title level={3}>Bites</Typography.Title></a></Col>
      <Col span={6}>
      <Image
          width={200}
          src={require('../../assets/images/projectpreview/theclone.PNG')}
          fallback="data:image/png;base64,"
        />
        <a href='https://the-clone-tmdb.netlify.app/'><Typography.Title level={3}>The Clone</Typography.Title></a></Col>
    </Row>
  </Row>
)

export default Projects;